import { Component, OnInit } from "@angular/core";
import { PoI18nService } from "@po-ui/ng-components";
import { DocumentationService } from "../services/documentation.service";
import { Subject, takeUntil } from "rxjs";
import { DocumentationLiterals } from "../models/documentation-literals";

@Component({
  selector: "app-documentation-tlpp",
  templateUrl: "./documentation-tlpp.component.html",
  styleUrls: ["./documentation-tlpp.component.css"],
})
export class DocumentationTlppComponent implements OnInit {
  categories;
  links;
  literals: DocumentationLiterals;
  destroy$: Subject<boolean> = new Subject<boolean>();

  constructor(
    private service: DocumentationService,
    private i18nService: PoI18nService
  ) {
    this.i18nService
      .getLiterals({
        context: "documentation",
        language: this.i18nService.getLanguage(),
      })
      .pipe(takeUntil(this.destroy$))
      .subscribe((literals: DocumentationLiterals) => {
        this.literals = literals;
      });
  }

  ngOnInit(): void {
    this.service
      .getDocumentation()
      .pipe(takeUntil(this.destroy$))
      .subscribe(data => {
        this.links = data;
        this.categories = this.parseCategories(data);
      });
  }

  parseCategories(obj) {
    var retorno = [];
    Object.keys(obj).forEach(key => {
      retorno.push(key);
    });
    return retorno;
  }

  getLinks(category) {
    let oLinks = this.links[category];
    if (Object.keys(oLinks).length > 0) {
      let teste = [];
      for (let i = 0; i < Object.keys(oLinks).length; i++) {
        let tst = {};
        Object.keys(oLinks[i]).forEach(key => {
          if (key === "url") {
            tst["action"] = this.redirectDocumentation.bind(
              this,
              oLinks[i][key]
            );
          } else {
            tst[key] = oLinks[i][key];
          }
        });
        teste.push(tst);
      }
      return teste;
    }
  }

  redirectDocumentation(content) {
    window.open(content, "_blank");
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}
