import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";

import {
  PoI18nConfig,
  PoI18nModule,
  PoModule,
  PoButtonModule,
  PoTableModule,
} from "@po-ui/ng-components";

import { DocumentationRoutingModule } from "./documentation-routing.module";
import { DocumentationBaseComponent } from "./documentation-base/documentation-base.component";
import { DocumentationService } from "./services/documentation.service";
import { DocumentationTlppComponent } from "./documentation-tlpp/documentation-tlpp.component";
import { documentationEn } from "../documentation/i18n/documentation-en";
import { documentationPt } from "../documentation/i18n/documentation-pt";
import { generalEn } from "../shared/i18n/general-en";
import { generalPt } from "../shared/i18n/general-pt";
import { SharedModule } from "../shared/shared.module";

const i18nConfig: PoI18nConfig = {
  contexts: {
    general: {
      "pt-BR": generalPt,
      "en-US": generalEn,
    },
    documentation: {
      "pt-BR": documentationPt,
      "en-US": documentationEn,
    },
  },
  default: {
    language: "pt-BR",
    context: "general",
    cache: true,
  },
};
@NgModule({
  declarations: [DocumentationBaseComponent, DocumentationTlppComponent],
  imports: [
    CommonModule,
    DocumentationRoutingModule,
    PoModule,
    PoButtonModule,
    PoTableModule,
    SharedModule,
    PoI18nModule.config(i18nConfig),
  ],
  providers: [DocumentationService],
})
export class DocumentationModule {}
