import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { DocumentationBaseComponent } from "./documentation-base/documentation-base.component";
import { AuthGuard } from "../guards/auth.guard";
import { DocumentationTlppComponent } from "./documentation-tlpp/documentation-tlpp.component";

const routes: Routes = [
  {
    path: "",
    component: DocumentationBaseComponent,
    children: [
      {
        path: "native",
        component: DocumentationTlppComponent,
        canActivate: [AuthGuard],
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DocumentationRoutingModule {}
