import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Host } from "../../shared/components/host.component";

@Injectable({
  providedIn: "root",
})
export class DocumentationService {
  private readonly cUriService: string = "/tlpp/document/grouper";

  constructor(private http: HttpClient, private host: Host) {}

  getDocumentation() {
    return this.http.get<any>(`${this.host.setUrlService(this.cUriService)}`);
  }
}
