import { Component, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { HeaderData } from "src/app/shared/components/header/models/header-data.model";
import { HeaderService } from "src/app/shared/components/header/services/header.service";

@Component({
  selector: "app-documentation",
  templateUrl: "./documentation-base.component.html",
  styleUrls: ["./documentation-base.component.css"],
})
export class DocumentationBaseComponent implements OnInit {
  headerData: HeaderData = {
    title: "Documentações Disponíveis",
    icon: "po-icon po-icon-map",
    routeUrl: "native",
  };

  constructor(
    private route: Router,
    private router: ActivatedRoute,
    private headerService: HeaderService
  ) {
    this.headerService.setheaderData(this.headerData);
  }

  ngOnInit(): void {
    this.route.navigate(["native"], { relativeTo: this.router });
  }
  teste(link: string): void {
    this.route.navigate([link], { relativeTo: this.router });
  }
}
